import React from "react";

export function TruthTable({ children, ...props }) {
  let rows = children.props.children.split("\\newline");

  rows = rows.map((row) => {
    let cells = row.split(",");

    cells = cells.map((cell) => {
      let animate = cell.includes("*") ? true : false;

      let divides = cell.includes("|") ? true : false;

      return (
        `<td class='${animate ? "fragment" : ""}${divides ? "divides" : ""}'>` +
        cell.replace(/[*|]/, "") +
        "</td>"
      );
    });
    return "<tr>" + cells.join("") + "</tr>";
  });

  console.log();

  return <table className="truth-table" dangerouslySetInnerHTML={{ __html: rows.join("") }} />;
}
